import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import './Game1.scss';

const Game1 = () => {
  const [isLoaded, setIsLoaded] = useState(false);
  const [markerFound, setMarkerFound] = useState(false);
  const navigate = useNavigate();

  useEffect(() => {
    // Check for AFRAME and set isLoaded to true when available
    const interval = setInterval(() => {
      if (window.AFRAME?.registerComponent) {
        setIsLoaded(true);
        clearInterval(interval);
      }
    }, 100);

    return () => clearInterval(interval);
  }, []);

  useEffect(() => {
    if (!isLoaded) return;

    const marker = document.querySelector('a-marker');

    if (!marker) return;

    const handleMarkerFound = () => setMarkerFound(true);
    const handleMarkerLost = () => setMarkerFound(false);

    marker.addEventListener('markerFound', handleMarkerFound);
    marker.addEventListener('markerLost', handleMarkerLost);

    return () => {
      marker.removeEventListener('markerFound', handleMarkerFound);
      marker.removeEventListener('markerLost', handleMarkerLost);
    };
  }, [isLoaded]);

  useEffect(() => {
    const scene = document.querySelector('a-scene');
    return () => {
      if (scene) {
        scene.parentNode.removeChild(scene);
      }
    };
  }, []);

  useEffect(() => {
    // Add hide-background class to hide background images
    document.body.classList.add('hide-background');

    // Remove hide-background class on component unmount
    return () => {
      document.body.classList.remove('hide-background');
    };
  }, []);

  const handleEndGame = () => {
    navigate('/', { state: { dialogIndex: 5 } });
  };

  return (
    <div className="container1">
      {isLoaded ? (
        <a-scene embedded arjs="sourceType: webcam;">
          <a-marker preset="hiro" arjs="debugUIEnabled: false;">
            <a-box position="0 0.5 0" width="0.2" height="0.2" depth="0.2" color="red"></a-box>
          </a-marker>
          <a-entity camera></a-entity>
        </a-scene>
      ) : (
        <p>Loading AR...</p>
      )}
      {markerFound && (
        <button className="button" onClick={handleEndGame}>
          遊戲結束
        </button>
      )}
    </div>
  );
};

export default Game1;
